import * as React from "react";
import { Breadcrumbs, Button, ButtonGroup, Hidden, IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Typography } from "@material-ui/core";
import Navbar from "../../../components/ui/Navbar";
import { Link } from "gatsby";
import AddIcon from '@material-ui/icons/Add';
import { navigate } from "gatsby";


import Infra from '@material-ui/icons/Receipt';
import Serv from '@material-ui/icons/Info';
import Umkm from '@material-ui/icons/Description';

export const categories = [{
    type: 'pernyataan',
    title: 'Surat Pernyataan',
    icon: Infra,
}, {
    type: 'keterangan',
    title: 'Surat Keterangan',
    icon: Serv,
}, {
    type: 'permohonan',
    title: 'Surat Permohonan',
    icon: Umkm,
}];

export const keyedCategories = categories.reduce((a, b) => {
    a[b.type] = b;
    return a;
}, {})

const Page = () => {
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Typography color="textPrimary">Layanan Surat</Typography>
                    </Breadcrumbs>
                    <ListItemSecondaryAction>
                        <Button startIcon={<AddIcon />} onClick={() => navigate('/desa/surat/choose')} variant="contained" color="primary">
                            Minta Surat
                        </Button>
                    </ListItemSecondaryAction>
                </ListItem>
            </List>
        </div>
    );
}

export default Page
