import * as React from "react";
import { Breadcrumbs, Button, ButtonGroup, FormControl, FormHelperText, Hidden, IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Switch, TextField, Typography } from "@material-ui/core";
import Navbar from "../../../components/ui/Navbar";
import { Link } from "gatsby";
import AddIcon from '@material-ui/icons/ArrowLeft';
import { navigate } from "gatsby";
import { getSearchParam, winconfirm } from "../../../components/util";
import { useFetcher } from "../../../components/ajax";
import Defer from "../../../components/ui/Defer";
import { keyedCategories } from ".";
import { useUserInfo } from "../../../components/save";
import AddressInput from "../../../components/ui/market/AddressInput";

const Page = () => {
    const user = useUserInfo();
    const id = getSearchParam().get('id') || null;
    const code = getSearchParam().get('code') || null;
    const item = useFetcher(id && '/desa/ask/' + id);
    const info = useFetcher((!id || item) && '/desa/mail/services/' + (id ? item.data.code : code));
    const [address, setAddress] = React.useState(null);
    const [forms, setForms] = React.useState({});
    const [delivery, setDelivery] = React.useState(true);
    React.useEffect(() => {
        if (item) {
            setForms(item.data.forms);
        } else if (info) {
            setForms(info.data.forms.reduce((a, b) => {
                a[b.name] = '';
                return a;
            }, {}));
        }
        if (user)
            setAddress(user.account.address);
    }, [info, item]);
    const save = () => {
    };
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Link color="inherit" to="/desa/surat">
                            Layanan Surat
                        </Link>
                        <Typography color="textPrimary">Buat</Typography>
                    </Breadcrumbs>
                    <ListItemSecondaryAction>
                        <Button startIcon={<AddIcon />} onClick={() => navigate('/desa/surat/choose')} variant="contained">
                            Pilih Surat
                        </Button>
                    </ListItemSecondaryAction>
                </ListItem>
                <Defer data={info}>
                    {() => (
                        <>
                            <ListItem divider>
                                {keyedCategories[info.data.category] && React.createElement(keyedCategories[info.data.category].icon, { style: { fontSize: '2em', marginRight: 15 } })}
                                <ListItemText primary={info.data.title} secondary={info.data.description} />
                            </ListItem>
                            <ListItem>
                                <ListItemText primary="Pemohon" />
                                <ListItemSecondaryAction>
                                    <FormControl fullWidth style={{ minWidth: '50vw' }}>
                                        <TextField fullWidth disabled value={user?.nik} />
                                        <FormHelperText>{user?.name}</FormHelperText>
                                    </FormControl>
                                </ListItemSecondaryAction>
                            </ListItem>
                            {info.data.forms.map(item => (<ListItem key={item.name}>
                                <ListItemText primary={item.label} />
                                <ListItemSecondaryAction>
                                    <FormControl fullWidth style={{ minWidth: '50vw' }}>
                                        <TextField fullWidth value={forms[item.name] || ''} onChange={(e) => {
                                            forms[item.name] = e.target.value;
                                            setForms({ ...forms });
                                        }} />
                                    </FormControl>
                                </ListItemSecondaryAction>
                            </ListItem>))}
                            <ListItem divider />
                            <ListItem>
                                <ListItemText primary="Diantar?" />
                                <ListItemSecondaryAction>
                                    <Switch checked={delivery} onChange={(e) => setDelivery(e.target.checked)} />
                                </ListItemSecondaryAction>
                            </ListItem>
                            {delivery && <ListItem>
                                <ListItemText primary="Alamat" />
                                <ListItemSecondaryAction>
                                    <AddressInput value={address} onChange={setAddress} />
                                </ListItemSecondaryAction>
                            </ListItem>}
                            <ListItem>
                                <ListItemText primary="Ajukan" />
                                <ListItemSecondaryAction>
                                    <ButtonGroup>
                                        <Button onClick={save} variant="contained" color="primary">
                                            Ajukan Surat
                                        </Button>
                                    </ButtonGroup>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </>
                    )}
                </Defer>
            </List>
        </div>
    );
}

export default Page
